<template>
	<view class="previewImage" v-if="show" @click="doubleClick" @touchmove.stop.prevent>
		<swiper :circular='true' class="swiper" :vertical="vertical" :current="current" @change="swiperChange"
			:disable-touch="swiper">
			<swiper-item @touchmove.stop="isMove" v-for="(item, i) in list" :key="i" :id="i">
				<!-- <image mode="widthFix" class="bgImg0" :src="item" /> -->
				<movable-area class="marea" scale-area>
					<movable-view :id="'movable-view-'+i" :key="'movable-view-'+i" class="mview" direction="all"
						:out-of-bounds="true" :inertia="true" :scale="true" scale-min="0.7"
						scale-max="4" :scale-value="scale" @scale="onScale" @change="movableChange"
						@touchmove="handletouchmove" @touchstart="handletouchstart" @touchend="handletouchend">
						<image mode="widthFix" :id="'image-'+i" :key="'movable-view'+i" class="image" :src="item"
							:data-index="i" :data-src="item" />
					</movable-view>
				</movable-area>
			</swiper-item>
		</swiper>
		<view class="page" v-if="list.length > 0">
			<text class="text">{{ index + 1 }} / {{ list.length }}</text>
		</view>
		<!-- <view @touchmove="handletouchmove" @touchstart="handletouchstart" @touchend="handletouchend" class="desc" v-if="list.length > 0">
				{{ list[index].pictureDescription }}
			</view> -->
	</view>
</template>

<script>
	export default {
		name: 'pagePreviewImage',
		props: {
			list: {
				//ÂõæÁâáÂàóË°®
				type: Array,
				required: true,
				default: () => {
					return [];
				}
			},
			// ÊòØÂê¶Á´ñÂ±è
			vertical: {
				type: Boolean,
				default: false
			}
		},
		data() {
			return {
				timeStamp:0,
				isOnScale:false,
				swiper: false, //ÊòØÂê¶Á¶ÅÁî®
				show: false, //ÊòæÁ§∫Áä∂ÊÄÅ
				current: 0, //ÂΩìÂâçÈ°µ
				index: 0, //ÂΩìÂâçÈ°µ
				time: 0, //ÂÆöÊó∂Âô®
				interval: 1000, //ÈïøÊåâ‰∫ã‰ª∂
				scale: 1, //Áº©ÊîæÊØî‰æã
				old: {
					scale: 1
				},
				// ÂàöËß¶Á¢∞ÁöÑÊó∂Èó¥
				startTime: 0,
				// ËÆ∞ÂΩïÊâãÊåáÁÇπÂáªÁöÑ‰ΩçÁΩÆÔºâ
				startData: {
					clientX: '',
					clientY: ''
				},
				isZoom: false, // ÊòØÂê¶Áº©ÊîæËøá
				lock: false, // ÂÖÅËÆ∏ÊªëÂä®
				isTouchOutOfBounds: null,
				isOutOfBounds: null,
				lastTapTimeoutFunc: null, // ËÆ∞ÂΩïÂèåÂáª‰∫ã‰ª∂
				lastTapDiffTime: 0,
				isScale: 1 // ÁúüÂÆûÁöÑÊØî‰æãÂèòÂåñ
			};
		},
		watch: {
			isScale: {
				handler(newVal, objVal) {
					if (newVal > 1.000001) {
						this.lock = true // ÂΩìÂõæÁâáÁº©ÊîæÂÄºÂ§ß‰∫é1Êó∂Ôºå‰∏çÂÖÅËÆ∏ÊªëÂä®
					} else {
						this.lock = false
						if (objVal > 1) {
							this.isZoom = true;
						}
					}
				},
				deep: true
			}
		},
		methods: {
			closeIImg() {
				this.$emit('closeIImg1', false)
			},
			//ÊØî‰æãÂèòÂåñ
			onScale(e) {
				
				this.isScale = e.target.scale
				this.old.scale = e.target.scale
			},
			// Á¶ÅÊ≠¢swiperËá™Â∏¶ÁöÑÊªöÂä®‰∫ã‰ª∂
			isMove() {
				return
			},

			//ÈïøÊåâ‰∫ã‰ª∂Áõ∏ÂÖ≥ÂÜÖÂÆπ---------ÂºÄÂßã-------------------
			//Êé•Ëß¶ÂºÄÂßã
			handletouchstart(e) {
				if (this.isZoom) {
					this.isZoom = false
				}
				// Ëé∑ÂèñÂàùÂßãÊó∂Èó¥
				this.startTime = Date.now()
				// Ëé∑ÂèñÂàùÂßãÁöÑ‰ΩçÁΩÆ
				this.startData.clientX = e.changedTouches[0].clientX;
				this.startData.clientY = e.changedTouches[0].clientY;

				var tchs = e.touches.length;
				if (tchs != 1) {
					return false;
				}
				this.time = setTimeout(() => {
					this.onLongPress(e);
				}, this.interval);
				return false;
			},
			//Ê∏ÖÈô§ÂÆöÊó∂Âô®
			handletouchend(event) {
				this.isOnScale = true;
				// Â¶ÇÊûúÂõæÁâáÁº©ÊîæËøáÔºåÁõ¥Êé•Ë∑≥Âá∫ÔºàÂõæÁâáÁº©ÊîæÂêéËΩÆÊí≠ÂõæÁâá‰ºöÂØºËá¥Ê≠§ÊñπÊ≥ïÊâßË°å‰∏§Ê¨°ÔºåÂÖ∑‰ΩìÂéüÂõ†‰∏çÊòé[ÂèØËÉΩÊàëÁ¨®Ê≤°ÂèëÁé∞üòÇ]Ôºâ
				// ËøôËæπÁî®isZoomÊã¶Êà™‰∏Ä‰∏ã
				if (this.isZoom) {
					return
				}

				const endTime = Date.now()
				if (endTime - this.startTime > 2000) {
					// Â¶ÇÊûúÊâãÊåáÊªëÂä®ÁöÑË∑ùÁ¶ªË∂ÖËøá2s Â∞±ÈªòËÆ§‰∏çÂêàÊ≥ï
					return;
				}
				let elePosition = 'ÊöÇÊó†‰ΩçÁΩÆ'
				// ËÆ∞ÂΩïÊâãÊåáÊªëÂä®ÁöÑÊñπÂêë
				const subX = event.changedTouches[0].clientX - this.startData.clientX;
				const subY = event.changedTouches[0].clientY - this.startData.clientY;
				if(Math.abs(subX)<20 || Math.abs(subY)<20){
					this.isOnScale = false;
				}
				if (subY > 50 || subY < -50) {
					if (subY > 50) {
						console.log('‰∏ãÊªë')
						elePosition = '‰∏ãÊªë'
					} else if (subY < -50) {
						console.log('‰∏äÊªë')
						elePosition = '‰∏äÊªë'
					}
				} else {
					if (subX > 100 / this.isScale) {
						console.log('Âè≥Êªë')
						elePosition = 'Âè≥Êªë'
					} else if (subX < -100 / this.isScale) {
						console.log('Â∑¶Êªë')
						elePosition = 'Â∑¶Êªë'
					} else {
						console.log('Êó†Êïà')
					}
				}

				if (this.isTouchOutOfBounds && this.isOutOfBounds) {
					this.lock = false
				}

				if (this.vertical) { // Á´ñÂ±è
					if (elePosition == '‰∏ãÊªë' && !this.lock) {
						if (this.current == 0) {
							this.current = this.list.length - 1
						} else {
							this.current--
						}
					} else if (elePosition == '‰∏äÊªë' && !this.lock) {
						if (this.current == this.list.length - 1) {
							this.current = 0
						} else {
							this.current++
						}
					}
				} else { // Ê®™Â±è
					if (elePosition == 'Âè≥Êªë' && !this.lock) {
						if (this.current == 0) {
							this.current = this.list.length - 1
						} else {
							this.current--
						}
					} else if (elePosition == 'Â∑¶Êªë' && !this.lock) {
						if (this.current == this.list.length - 1) {
							this.current = 0
						} else {
							this.current++
						}
					}
				}


				clearTimeout(this.time);
				if (this.time != 0) {
					//Â§ÑÁêÜÁÇπÂáªÊó∂Èó¥
				}
				return false;
			},
			//Ê∏ÖÈô§ÂÆöÊó∂Âô®
			handletouchmove() {
				clearTimeout(this.time);
				this.time = 0;
			},
			// Â§ÑÁêÜÈïøÊåâ‰∫ã‰ª∂
			onLongPress(e) {
				// var src = e.currentTarget.dataset.src;
				// var index = e.currentTarget.dataset.index;
				// var data = {
				// 	src: src,
				// 	index: index
				// };
				// this.$emit('longPress', data);
			},
			//ÈïøÊåâ‰∫ã‰ª∂Áõ∏ÂÖ≥ÂÜÖÂÆπ---------ÁªìÊùü-------------------

			//ÂõæÁâáÊîπÂèò
			swiperChange(e) {
				console.log('ÂõæÁâáÊîπÂèò‰∫ã‰ª∂', e);
				// ÂõæÁâáÂàáÊç¢ÂêéÔºåÊääÁõ∏ÂÖ≥ÂèÇÊï∞ËøòÂéü
				this.isZoom = false
				this.lock = false
				this.index = e.target.current; //Êõ¥Êñ∞ÂΩìÂâçÂõæÁâáindex
				this.isScale = 1
				// this.scale = 1; //Áº©ÊîæÊØî‰æã
				// ËøôÊ†∑ÂÜôÊòØÂõ†‰∏∫ÁªÑ‰ª∂Â±ûÊÄßËÆæÁΩÆ‰∏çÁîüÊïà
				// ÂÖ∑‰ΩìÂèÇËÄÉ https://uniapp.dcloud.io/vue-api?id=componentsolutions
				this.scale = this.old.scale
				this.$nextTick(function() {
					this.scale = 1
				});
				this.isTouchOutOfBounds = null
				this.isOutOfBounds = null
				this.$emit('uploadIndex', this.index)
			},

			//ÁßªÂä®ÂèòÂåñ
			movableChange(e) {
				console.log(this.isOnScale)
				if (this.isScale == 1) {
					this.lock = false // ÂÖÅËÆ∏ÊªëÂä®
				} else {
					this.lock = true // ‰∏çÂÖÅËÆ∏ÊªëÂä®
				}

				if (this.isScale != 1) {
					if (!this.isTouchOutOfBounds && e.detail.source == 'touch-out-of-bounds') {
						this.isTouchOutOfBounds = 'touch-out-of-bounds'
					} else if (!this.isOutOfBounds && e.detail.source == 'out-of-bounds') {
						this.isOutOfBounds = 'out-of-bounds'
					}
				}
			},
			//ÊâìÂºÄ
			open(e) {
				console.log(!isNaN(e), this.list.length, e)
				if (e === null || e === '') {
					console.log('previewImage:ÊâìÂºÄÂèÇÊï∞Êó†Êïà');
					return;
				}

				if (!isNaN(e)) {
					if (e >= this.list.length) {
						console.log('previewImage:ÊâìÂºÄÂèÇÊï∞Êó†Êïà');
					} else {
						this.index = e;
						this.current = e;
					}
				} else {
					var index = this.list.indexOf(e);
					if (index === -1) {
						this.list = [e];
						this.index = 0;
						console.log('previewImage:Êú™Âú®ÂõæÁâáÂú∞ÂùÄÊï∞ÁªÑ‰∏≠ÊâæÂà∞‰º†ÂÖ•ÁöÑÂõæÁâáÔºåÂ∑≤‰∏∫‰Ω†Ëá™Âä®ÊâìÂºÄÂçïÂº†È¢ÑËßàÊ®°Âºè')
					} else {
						this.index = this.list.indexOf(e);
						this.current = this.list.indexOf(e);
					}
				}
				console.log('previewImage:ÂΩìÂâçÈ¢ÑËßàÂõæÁâáÂ∫èÂè∑' + this.index);
				this.lock = false
				this.show = true;
			},
			// Ëá™ÂÆö‰πâÂèåÂáª‰∫ã‰ª∂
			doubleClick(e) {
				console.log(e.changedTouches.length)
				this.lock = true
				let _this = this;
				let curTime = new Date().getTime();
				let lastTime = _this.lastTapDiffTime;
				_this.lastTapDiffTime = curTime;
				//‰∏§Ê¨°ÁÇπÂáªÈó¥ÈöîÂ∞è‰∫é300ms, ËÆ§‰∏∫ÊòØÂèåÂáª
				let diff = curTime - lastTime;
				if (diff < 300) {
					console.log("ÂèåÂáª", this.isScale)
					// Â¶ÇÊûúÂõæÁâáÂΩìÂâçÊ≤°ÊúâÊîæÂ§ßÂàôÂèåÂáªÊîæÂ§ß‰∏§ÂÄç
					if (this.isScale == 1) {
						this.isScale = 2
						this.scale = 2
					} else {
						// Âê¶ÂàôÂèåÂáªËøòÂéü
						this.swiperChange(e)
					}

					clearTimeout(_this.lastTapTimeoutFunc); // ÊàêÂäüËß¶ÂèëÂèåÂáª‰∫ã‰ª∂Êó∂ÔºåÂèñÊ∂àÂçïÂáª‰∫ã‰ª∂ÁöÑÊâßË°å
				} else {
					// ÂçïÂáª‰∫ã‰ª∂Âª∂Êó∂300ÊØ´ÁßíÊâßË°å
					if(this.isOnScale){
						return
					}
					_this.lastTapTimeoutFunc = setTimeout(() => {
						console.log("ÂçïÂáª")
						this.closeIImg()
					}, 300);
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.previewImage {
		width: 100%;
		height: 100%;

		.swiper {
			width: 100%;
			height: 100%;
			position: relative;

			.bgImg0 {
				position: absolute;
				width: 100%;
				top: 40%;
				transform: translateY(-50%);
				z-index: 1;
				// filter: blur(10rpx)
			}

			.marea {
				height: 100%;
				width: 100%;
				position: fixed;
				overflow: hidden;
				z-index: 3;

				.mview {
					display: flex;
					align-items: center;
					justify-content: center;
					width: 100%;
					height: auto;
					min-height: 100%;

					.image {
						width: 100%;
					}
				}
			}
		}

		.page {
			position: absolute;
			width: 100%;
			bottom: 60rpx;
			text-align: center;

			.text {
				color: #fff;
				font-size: 26rpx;
				background-color: rgba(0, 0, 0, 0.5);
				padding: 3rpx 16rpx;
				border-radius: 20rpx;
				user-select: none;
			}
		}

		.desc {
			position: fixed;
			bottom: 40rpx;
			width: 100%;
			height: 165rpx;
			box-sizing: border-box;
			padding: 5rpx 30rpx;
			display: -webkit-box;
			-webkit-box-orient: vertical;
			-webkit-line-clamp: 5;
			overflow: hidden;
			color: #fff;
			font-size: 28rpx;
			letter-spacing: 3rpx;
			user-select: none;
		}
	}
</style>
